import React from 'react'



const Heroslidercard = (props) => {


  return (
              <>
                   <div className="papper">
                    <div className="row">
                        <div className="col-md-6 order-md-0 order-1 d-flex align-items-center justify-content-center" data-aos="fade-up">
                            <div className="cation ">
                                 <h6>{props.stitle}</h6>
                                <h1>{props.heading}</h1>
                                <p className='cols'>{props.para}</p>
                                <a href={props.links} target="_blank">
                                <button className="btn btnReadm">{props.btnname}</button>
                                </a>
                            </div>
                        </div>
                        <div className="col-md-6 order-md-1 order-0" data-aos="fade-left">
                            <img src={props.imgser} className="d-block w-100" alt="slider1"/>
                        </div>
                        </div>
                   </div>
                 
              </>
  )
}

export default Heroslidercard;
