import React from 'react'
import Heroslidercard from './Heroslidercard';
import Herosliderdata from './HeroslidercardData';

const Hero = () => {


  return (
     <>
        <section className="hero">
            <div className="container">
                <div className="row">
                    <div className='col-md-12'>
                        <div id="carouselExampleDark" className="carousel carousel-dark slide" data-bs-ride="carousel">
                        <div className="carousel-indicators">
                        <button type="button" data-bs-target="#carouselExampleDark" data-bs-slide-to="0" className="active" aria-current="true" aria-label="Slide 1"></button>
                        <button type="button" data-bs-target="#carouselExampleDark" data-bs-slide-to="1" aria-label="Slide 2"></button>
                        <button type="button" data-bs-target="#carouselExampleDark" data-bs-slide-to="2" aria-label="Slide 3"></button>
                        <button type="button" data-bs-target="#carouselExampleDark" data-bs-slide-to="3" aria-label="Slide 4"></button>
                        </div>
                        <div className="carousel-inner">

                          {Herosliderdata.map((val,i) =>{
                            return(
                                
                                  <div key={i} className={i === 0 ? "carousel-item active" : "carousel-item"} data-bs-interval="10000">
                                   <Heroslidercard key={val.id} stitle={val.stitle} heading={val.heading} para={val.para} links={val.links} btnname={val.btnname} imgser={val.imgser}/> 
                                   </div>
                              
                            );
                          })}
                          
                        </div>
                       </div>
                    </div>
                    </div>
                </div>
        
        </section>
     </>
  )
}

export default Hero;
