import React from 'react'
import WorkData from './WorkData';
import { BsSearch } from "react-icons/bs";

const Work = () => {

    const WorkCard = (props) =>{

        let mystyle = {
            display : "none",
        }

        return(
            <div className="col-md-3" data-aos="flip-left"
            data-aos-easing="ease-out-cubic"
            data-aos-duration="2000">
            <div className="card">
                <img src={props.imgsc} alt="work images" />
                <div className="card_caption" style={mystyle}>
                    <BsSearch className='icons_s'/>
                </div>
            </div>
        </div>
        );
    }


  return (
     <section className="work py-5">
        <div className="container py-3">
            <div className="row">
                <div className="col-md-12 text-center">
                    <h6 data-aos="fade-up">We’ve done lot’s of work, Let’s </h6>
                    <h2 data-aos="fade-up">Check some from here</h2>
                </div>
            </div>
            <div className="row pt-4">
               {WorkData.map((val)=>{
                return(
                    
                       <WorkCard key={val.id} imgsc={val.imgsc}/>
                
                );
               })}
            </div>
        </div>
     </section>
  )
}

export default Work;
