import React from 'react'

const GetinTouch = () => {

    let mapLati = "https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d121058.92836615497!2d73.79292730116279!3d18.524766325796897!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x3bc2bf2e67461101%3A0x828d43bf9d9ee343!2sPune%2C%20Maharashtra!5e0!3m2!1sen!2sin!4v1655193165708!5m2!1sen!2sin"; 
    

   let styleMy = {
        width : "100%",
        height: "400px",
        border : "0",
        allowfullscreen:"",
        loading:"lazy",
        referrerpolicy:"no-referrer-when-downgrade",
    
    }

  return (
    <>
        <section className="getintouch">
            <div className="container">
                <div className="row">
                    <div className="col-md-12 text-center">
                        <h3 className='mb-5'data-aos="fade-up">Get in touch</h3>
                    </div>
                </div>
            </div>
            <div className="row w-100 g-0">
                    <div className="col-md-6 firstone d-flex align-items-center pe-0 pe-md-5" data-aos="fade-right">
                       <div className='froms'>
                            <form className='row align-items-center'>
                                <div className="mb-3 col-md-6">
                            <input type="text" className="form-control" placeholder='Name'/>
                                </div>
                                <div className="mb-3 col-md-6">
                            <input type="email" className="form-control" placeholder='Email id'/>
                                </div>
                                <div className="mb-3 col-md-12">
                                <input type="text" className="form-control" placeholder='Subject'/>
                                </div>
                                <div className="mb-3 col-md-12">
                                <input type="text" className="form-control" placeholder='Message'/>
                                </div>
                                <div className="mb-3 col-md-12 ctnbtn">
                                   <button className="btn btnReadm">Submit</button>
                                </div>
                               
                            </form>
                       </div>
                    </div>
                    <div className="col-md-6 d-flex">
                       <iframe src={mapLati} style={styleMy}/>
                    </div>
                </div>
        </section>
    </>
  )
}

export default GetinTouch;
