import React from 'react'
import WhatweDoCard from './WhatweDoCard';
import WhatCardData from './WhatweCardData';

const Whatwedo = () => {



  return (
     <>
        <main className="whatWedo py-5 text-center">
            <div className="container">
                <div className="row">
                    <div className="col-md-12">
                       <h2 data-aos="fade-up">What We Do</h2>
                    </div>
                </div>
                <div className='row'>
                  
                  {WhatCardData.map((value) =>{
                    return(
                        
                           <WhatweDoCard key={value.id} icon={value.icon} htitle={value.htitle} disc={value.disc}/>
                        
                    );
                  })}

                   
                </div>
            </div>
        </main>
     </>
  )
}

export default Whatwedo;
