import React from 'react'
import Navbar from "../component/Navbar";
import Footer from '../component/Footer';
import contImg from '../images/contactus.jpg';
import TextField from '@mui/material/TextField';
import Button from '@mui/material/Button';
const Contact = () => {
  return (
    <>
    <Navbar/>
      <section className="about py-5">
            <div className="container">
                <div className="row align-items-center">
                    <div className="col-md-6 textC" data-aos="fade-right">
                      <div className="about_inner">
                          <h1>Contact Us</h1>
                          <span className='cBorder'></span>
                      </div>
                     
                      <div className="row mt-5">
                        
                         <div className="col-md-6 mb-3">
                             <TextField fullWidth id="outlined-basic" label="First Name" variant="outlined" />
                          </div>
                          <div className="col-md-6 mb-3">
                          <TextField fullWidth id="outlined-basic" label="Last Name" variant="outlined" />
                          </div>
                          <div className="col-md-6 mb-3">
                             <TextField fullWidth id="outlined-basic" label="Phone" variant="outlined" />
                          </div>
                          <div className="col-md-6 mb-3">
                          <TextField fullWidth id="outlined-basic" label="Email Id" variant="outlined" />
                          </div>

                          <div className="col-md-12 mb-3">
                          <TextField fullWidth id="outlined-basic" label="Message" variant="outlined" />

                          <Button className='mt-4' variant="contained">Submit</Button>

                          </div>
                         

                      </div>
                    </div>
                    <div className="col-md-6 img_circle d-flex align-items-end justify-content-end" data-aos="zoom-in">
                      <img src={contImg}  className='img-fluid' alt="about" />
                     </div>
                </div>

            </div>
        </section>
        <Footer/>
  </>
  )
}

export default Contact;
