import React from 'react'
import logo from "../images/logo.png";
import { BiMenu } from "react-icons/bi";
import Button from '@mui/material/Button';
import Menu from '@mui/material/Menu';
import MenuItem from '@mui/material/MenuItem';
import { NavLink } from "react-router-dom";


const Navbar = () => {
 


  const [anchorEl, setAnchorEl] = React.useState(null);
  const open = Boolean(anchorEl);
  const handleClick = (event) => {
    setAnchorEl(event.currentTarget);
  };
  const handleClose = () => {
    setAnchorEl(null);
  };

  return (
    <>
        <nav className="navbar">
            <div className="container">
                <div className="row gx-0 w-100 align-items-center">
                       <div className="col-6 col-sm-6">
                          <img src={logo} className="img-fluid" alt="logo"  />
                        </div>
                        <div className="col-6 col-sm-6 d-flex justify-content-end">
                   <Button
                    id="basic-button"
                    aria-controls={open ? 'basic-menu' : undefined}
                    aria-haspopup="true"
                    aria-expanded={open ? 'true' : undefined}
                    onClick={handleClick}
                    >
                    <BiMenu className='toggle'/>
                    </Button>
                    <Menu
                    id="basic-menu"
                    anchorEl={anchorEl}
                    open={open}
                    onClose={handleClose}
                    MenuListProps={{
                    'aria-labelledby': 'basic-button',
                    }}
                    >
                    <MenuItem className='anch' onClick={handleClose}>
                        <NavLink to="/">Home</NavLink>
                    </MenuItem>
                    <MenuItem className='anch' onClick={handleClose}>
                        <NavLink to="/about">About</NavLink>
                    </MenuItem>
                    <MenuItem className='anch' onClick={handleClose}>
                        <NavLink to="/ourwork">Our Work</NavLink>
                    </MenuItem>
                    <MenuItem className='anch' onClick={handleClose}>
                        <NavLink to="/contact">Contact Us</NavLink>
                    </MenuItem>
                    </Menu>

                        </div>
                   </div>
            </div>
        </nav>

    
   
    </>
  )
}

export default Navbar;
