import React, { useState } from 'react'
import Navbar from "../component/Navbar";
import Footer from '../component/Footer';
import aboutImg from "../images/about.png";
const About = () => {

const [toggle, setToggle] = useState(true);

  return (
   <>
   <Navbar/>
      <section className="about py-5">
            <div className="container">
                <div className="row align-items-center">
                    <div className="col-md-6 textC" data-aos="fade-right">
                      <div className="about_inner">
                          <h1>About Us</h1>
                          <span className='cBorder'></span>
                      </div>
                      <p className='mt-3'>Lorem ipsum dolor, sit amet consectetur adipisicing elit. Veniam recusandae ullam excepturi iste! Ratione nemo sed harum facere natus magni, eum facilis, maxime molestias alias sunt amet nisi reiciendis voluptatum.</p>
                      <p>Lorem ipsum dolor sit amet consectetur adipisicing elit. Eveniet labore molestias esse odit corporis recusandae iure modi illo eum optio voluptatum soluta sed quam delectus impedit tempore non, dolor nihil?</p>
                      <button onClick={()=>setToggle(!toggle)}  className="btn btnReadm my-3">Read More</button>
                    </div>

                    <div className="col-md-6 img_circle d-flex align-items-end justify-content-end" data-aos="zoom-in">
                        <img src={aboutImg} style={{borderColor : toggle? "#fc301e":"green"}}  className='img-fluid' alt="about" />
                     </div>
                </div>

            </div>
        </section>
        <Footer/>
  </>
  )
}

export default About;
