import React from 'react'
import Navbar from "../component/Navbar";
import Footer from '../component/Footer';
import Card from '@mui/material/Card';
import CardContent from '@mui/material/CardContent';
import Typography from '@mui/material/Typography';
import { CardActionArea } from '@mui/material';
import Ourcdata from './Ourcdata';

const Ourwork = () => {

    const CardMediaData  = (props) =>{
        return(
            <>
            
                 <div className="col-md-3" data-aos="flip-left"
     data-aos-easing="ease-out-cubic"
     data-aos-duration="2000">
                        <Card>
                        <CardActionArea>
                        <img src={props.imgser} alt="alem" className='img-fluid' />
                        <CardContent>
                        <Typography gutterBottom variant="h5" component="div">
                         <h3>{props.title}</h3>
                        </Typography>
                        <Typography variant="body2" color="text.secondary">
                        <p>{props.discrip}</p>
                        </Typography>
                        </CardContent>
                        </CardActionArea>
                        </Card>
                    </div>
            </>
        );
    }
    
  return (
    <>
    <Navbar/>
      <section className="about py-5 bgcolor">
            <div className="container">
                <div className="row align-items-center">
                    <div className="col-md-12">
                      <div className="about_inner">
                          <h1 data-aos="fade-right">Our Work</h1>
                          <span className='border'></span>
                      </div>
                      <div className="row mt-4">
                         
                         {Ourcdata.map((value)=>{
                            return(
                                <>
                                  <CardMediaData key={value.id} imgser={value.imgser} title={value.title} discrip={value.discrip}/>
                                </>
                            );
                         })}
                         
                      </div>
                    </div>
                  
                </div>

            </div>
        </section>
        <Footer/>
    </>
  )
}

export default Ourwork;
