import React, { useState } from 'react'
import projectsImg from "../images/projectP.png";
import { BsFillPlayFill } from "react-icons/bs";


const PreviousProject = () => {

let youTubelink = "https://www.youtube.com/watch?v=cyp7d-jEvNA&ab_channel=BenJ.Lee";

const [links , setDatalink] = useState(youTubelink);

const changeVideo = () => {
     setDatalink(window.open(`${links}`, "_blank"));
}

  return (
     <>
        <section className="bussiness py-5">
            <div className="container">
                <div className="row">
                <div className="col-md-8 m-auto text-center">
                      <h6 data-aos="fade-up">Previous Projects </h6>
                        <h2 data-aos="fade-up">Our Case Studies</h2>
                        <p data-aos="fade-up">Lorem ipsum dolor sit amet consectetur adipisicing elit. Deleniti ullam beatae blanditiis voluptatum inventore saepe possimus earum repellat neque, distinctio, rerum odit recusandae cupiditate ab tenetur reiciendis odio soluta illo.</p>
                        <div className='videoact' data-aos="flip-left">
                           <img src={projectsImg} className="img-fluid mt-4" alt="Porject" />
                           <div className='paly'>
                              <span onClick={changeVideo}><BsFillPlayFill className='playbtn'/></span>
                           </div>
                        </div>
                       <div className="btn btnReadm mt-5">Read More</div>
                    </div>
                </div>
            </div>
        </section>
     </>
  )
}

export default PreviousProject;
